import React, { Component } from 'react';
// import './ProductDetail.css';
import productImg from '../../public/img/product-1.jpg';
import { FormGroup, ControlLabel, FormControl } from "react-bootstrap";
import styles from './ProductDetail.module.css';
import { withRouter } from "react-router-dom";

class ProductDetail extends Component {
  handleClick = () => {
    const { history} = this.props;
    history.push('/shoppingCart');
  }
  render() {
    return (
      // <div className="page ProductDetail">
      <div className={`page ${[styles.page, styles.ProductDetail].join(' ')}`}>
        <div className={`block ${[styles.block, styles.titleBlock].join(' ')}`} >
          <div className={styles.imgBlock}>
            <img src={productImg}></img>
          </div>
          <div className={styles.description}>
          <div className={styles.column}>
              <label className={styles.title}>商品標題:</label>
              <p className={styles.content}>Lorem ipsum dolor</p>
            </div>
            <div className={styles.column}>
              <label className={styles.title}>尺寸:</label>
              <p className={styles.content}>L</p>
            </div>
            <div className={styles.column}>
              <label className={styles.title}>顏色:</label>
              <p className={styles.content}>紅</p>
            </div>
            <FormGroup className={styles.column} controlId="formControlsSelect">
              <ControlLabel className={styles.title}>Select</ControlLabel>
              <FormControl className={[styles.content, styles.quantity].join(' ')} componentClass="select">
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
              </FormControl>
            </FormGroup>
            <div className={`btnBlock ${styles.btnBlock}`}>
              <div className="btn putInCart">加入購物車</div>
              <div className="btn checkout"  onClick={this.handleClick}>結帳</div>
            </div>
          </div>
        </div>
        <div className={`block ${[styles.block, styles.contentBlock].join(' ')}`}>
          <p>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Deserunt voluptates rerum eveniet sapiente repellat esse, doloremque quod recusandae deleniti nostrum assumenda vel beatae sed aut modi nesciunt porro quisquam voluptatem.
          </p>
          <img src={productImg}></img>
          <img src={productImg}></img>
          <img src={productImg}></img>
          
        </div>
      </div>
    );
  }
}

export default withRouter(ProductDetail);
