import React, { Component } from 'react';
import styles from './ShoppingCart.module.css';
import productImg from '../../public/img/product-1.jpg';

class ShoppingCart extends Component {
  render() {
    return (
      <div className={`page ${styles.shoppingCart}`}>
        <div className={`block ${styles.item}`}>
          <img src={productImg} alt="productimg"></img>
          <div className={styles.description}>
            <div className={styles.column}>
              <label className={styles.title}>商品標題:</label>
              <p className={`${styles.content} title`}>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Deserunt voluptates rerum eveniet sapiente repellat esse, doloremque quod recusandae deleniti nostrum assumenda vel beatae sed aut modi nesciunt porro quisquam voluptatem.</p>
            </div>
            <div className={styles.column}>
              <label className={styles.title}>尺寸:</label>
              <p className={`${styles.content} size`}>L</p>
            </div>
            <div className= {styles.column}>
              <label className={styles.title}>顏色:</label>
              <p className={`${styles.content} color`}>紅</p>
            </div>
            <div className={`btn ${styles.btn} btn-danger`}>刪除</div>
          </div>
        </div>
        <div className={`block ${styles.item}`}>
          <img src={productImg} alt="productimg"></img>
          <div className={styles.description}>
            <div className= {styles.column}>
              <label className={styles.title}>商品標題:</label>
              <p className={`${styles.content} title`}>Lorem ipsum dolor</p>
            </div>
            <div className= {styles.column}>
              <label className={styles.title}>尺寸:</label>
              <p className={`${styles.content} size`}>L</p>
            </div>
            <div className= {styles.column}>
              <label className={styles.title}>顏色:</label>
              <p className={`${styles.content} color`}>紅</p>
            </div>
            <div className={`btn ${styles.btn} btn-danger`}>刪除</div>
          </div>
        </div>
        <div className={`block ${styles.item}`}>
          <img src={productImg} alt="productimg"></img>
          <div className={styles.description}>
            <div className= {styles.column}>
              <label className={styles.title}>商品標題:</label>
              <p className={`${styles.content} title`}>Lorem ipsum dolor </p>
            </div>
            <div className= {styles.column}>
              <label className={styles.title}>尺寸:</label>
              <p className={`${styles.content} size`}>L</p>
            </div>
            <div className= {styles.column}>
              <label className={styles.title}>顏色:</label>
              <p className={`${styles.content} color`}>紅</p>
            </div>
            <div className={`btn ${styles.btn} btn-danger`}>刪除</div>
          </div>
        </div>
        <div className="btnBlock">
          <div className={`btn ${[styles.btn, styles.checkout].join(' ')}`}>結帳</div>
        </div>
      </div>
    );
  }
}

export default ShoppingCart;
