import React, { Component } from 'react';
import './NavItem.css';
// import { Navbar, NavItem } from "react-bootstrap";
import { NavItem as NavItemBlock } from "react-bootstrap";
import { Route, Link } from 'react-router-dom';

class NavItem extends Component {
  render() {
    const {to, exact, label} = this.props;
    return (
      <Route
        path={to}
        exact={exact}
        children={({ match }) => (
          <NavItemBlock href="/" className={match ? "active" : ""} componentClass={Link} to={to}>
            {label}
          </NavItemBlock>
        )}
      />
    );
  }
}

export default NavItem;
