import React, { Component } from 'react';
import './ProductList.css';
import Product from '../product';

class ProductList extends Component {
  render() {
    return (
      <div className="page productList">
        <div className="filter">
          <ul>
            <li>BAPE</li>
            <li>SUPREME</li>
            <li>SUPREME</li>
            <li>SUPREME</li>
            <li>SUPREME</li>
            <li>SUPREME</li>
          </ul>
        </div>
        <div className="block productBlock">
          <Product></Product>
          <Product></Product>
          <Product></Product>
          <Product></Product>
          <Product></Product>
          <Product></Product>
        </div>
      </div>
    );
  }
}

export default ProductList;
