import React, { Component } from 'react';
import './Nav.css';
// import { Navbar, NavItem } from "react-bootstrap";
import NavItem from '../navItem'

import { Navbar, Nav as NavBlock } from "react-bootstrap";
import { HashRouter as Router, Route, Link } from 'react-router-dom';

class Nav extends Component {
  render() {
    return (
      <Router>
        <Navbar>
          <Navbar.Header>
            <Navbar.Brand>
              <Link className="navbar-brand" to="/">Normal</Link>
            </Navbar.Brand>
          </Navbar.Header>
          <NavBlock>
            <NavItem to="/productList" label='商品列表' />
            <NavItem to="/shoppingCart" label='購物車' />
          </NavBlock>
        </Navbar>
      </Router>
    );
  }
}

export default Nav;
